.NextPage{
    position: absolute;
    right: 0vw;
    height: 22px;
    width: 50px;
    background-color: inherit;
    border: 2px solid rgb(177, 177, 177);
    animation: Fade 2s;
    color: rgb(177, 177, 177);
}

.NextPage p {
    padding: 0;
    margin: 0;
    font-size: 13px;
    font-family: 'Poppins', sans-serif;
    transition: 600ms;
}

.NextPage:hover{
    background-color: rgb(177, 177, 177);
    color: rgba(27, 30, 33, 1);
    cursor: pointer;
}

.NextPage p:after{
    font-size: 14px;
    content:'Next';
}
.NextPage p:hover:after{
    font-size: 14px;
    content:'> Next';
}



.PrevPage{
    position: absolute;
    left: 0vw;
    height: 22px;
    width: 50px;
    background-color: inherit;
    border: 2px solid rgb(177, 177, 177);
    animation: Fade 2s;
    color: rgb(177, 177, 177);
}


.PrevPage p {
    padding: 0;
    margin: 0;
    font-size: 13px;
    font-family: 'Poppins', sans-serif;
    transition: 600ms;
}

.PrevPage:hover{
    background-color: rgb(177, 177, 177);
    color: rgba(27, 30, 33, 1);
    cursor: pointer;
}

.PrevPage p:after{
    font-size: 14px;
    content:'Prev';
}
.PrevPage p:hover:after{
    font-size: 14px;
    content:'Prev <';
}

.Lang {
    position: absolute;
    top: 0vh;
    height: 15px;
    width: 45px;
    background-color: inherit;
    border: 2px solid rgb(0, 0, 0);
    animation: Fade 2s;
    color: rgb(177, 177, 177);
    transform: scale(0.7);
}

.LangDiv {
    width: fit-content;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 5px;
}

.Lang:hover{
    transform: scale(0.8);
}

.Lang img:hover{
    cursor: pointer;
}


.BtnTransition {
    transition: 300ms;
}

.LangBtnTransition {
    transition: 100ms;
}


@keyframes Fade {
    0%{ opacity: 0;}
    40%{ opacity: 0;}
    100%{opacity: 1;}
}

@keyframes Fade {
    0%{ opacity: 0;}
    40%{ opacity: 0;}
    100%{opacity: 1;}
}

@media only screen and (max-width: 1000px) {
    .Lang {
        width: 100%;
        border: none;
        transform: scale(1);
        margin-top: 3px;
        animation: Fade 1s;
    }

    .Lang:hover{
        transform: scale(1);
    }

    .LangDiv{
        width: 100%;
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    .LangDiv img{
        margin: 0 auto;
    }
}