.Lang {
    position: absolute;
    top: 0vh;
    height: 15px;
    width: 45px;
    background-color: inherit;
    border: 2px solid rgb(0, 0, 0);
    -webkit-animation: Fade 2s;
            animation: Fade 2s;
    color: rgb(177, 177, 177);
    transform: scale(0.7);
}

.LangDiv {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 5px;
    -webkit-column-gap: 5px;
            column-gap: 5px;
}

.Lang:hover{
    transform: scale(0.8);
}

.Lang img:hover{
    cursor: pointer;
}


